<script>
import MailForm from '@/components/MailForm'
import ProcessDev from '../components/ProcessDev'
import SectionPortfolio from '../components/SectionPortfolio'
import OurFeatures from '../components/OurFeatures'
import Prices from '../components/Prices'
export default {
  components: { MailForm, ProcessDev, SectionPortfolio, Prices, OurFeatures },
  props: ['portfolio'],
  data () {
    return {
      priceProducts: [
        { name: 'Лайт', price: '1 000', feature1: '2d инфографика', feature2: 'Простые элементы', feature3: 'Статичная графика', feature4: 'Небольшой объем контента' },
        { name: 'Профессиональный', price: '5 000', feature1: '3d графика или сложное 2d / изометрия', feature2: 'Сложные элементы и высокая детализация', feature3: '3d анимация / видео', feature4: 'Контроль проекта со стороны CEO' },
        { name: 'Стандарт', price: '2 000', feature1: 'Изометрия или сложная 2d графика', feature2: 'Сложные элементы', feature3: 'Высокая детализация', feature4: 'Средний объем контента' }
      ]
    }
  },
  computed: {
    briefLink () {
      return this.$store.getters.briefLink
    }
  },
  methods: {
    scrollToTop() {
      window.scrollTo(0,0);
    },
    scrollToPrices () {
      let headerHeight = document.querySelector('.header').offsetHeight,
          pricesPosition = this.$el.querySelector('.prices').getBoundingClientRect().top - headerHeight,
          scrollOptions = {
            left: 0,
            top: pricesPosition,
            behavior: 'smooth'
          }
      window.scrollTo( scrollOptions)
    }
  },
  mounted() {
    this.$store.dispatch('formTopPosition')
  }
}
</script>

